$sm: "(max-width: 576px)";
$smover: "(min-width: 577px)";
$md: "(max-width: 768px)";
$mdover: "(min-width: 769px)";
$lg: "(max-width: 990px)";
$lgover: "(min-width: 991px)";
$xl: "(max-width: 1120px)";
$xlover: "(min-width: 1201px)";
$xxl: "(max-width: 1440px)";
$xxlover: "(min-width: 1441px)";
$xxxl: "(max-width: 1920px)";
$xxxlover: "(min-width: 1921px)";

$small: screen and #{$sm};
$medium: #{$smover} and #{$md};
$large: #{$mdover};

.rg-news-letter {
  max-width: 1208px;
  margin: auto auto;
  border: 1px solid #f2f2f2;
  border-radius: 10px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #fff;
}

.top {
  padding-top: 60px;
  background-image: url("./assets/images/rg_logo_simple@2x.png");
  background-repeat: no-repeat;
  background-position: top 40px right 50px;
  background-size: 40px;
  margin-bottom: 60px;
  @media #{$medium} {
    background-size: 30px;
    background-position: top 30px right 30px;
    padding-top: 50px;
    margin-bottom: 40px;
  }
  @media #{$small} {
    background-size: 25px;
    background-position: top 20px right 20px;
    margin-bottom: 30px;
  }
  .main-title {
    width: 480px;
    margin: auto auto;
    margin-bottom: 20px;
    padding: 0px 30px;
    @media #{$medium} {
      width: 280px;
      margin-bottom: 8px;
    }
    @media #{$small} {
      width: 180px;
      margin-bottom: 5px;
    }
    img {
      width: 100%;
    }
  }
  .version-info {
    width: 480px;
    margin: auto auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-image: url("./assets/images/line.png");
    background-size: 10px 1px;
    background-repeat: repeat-x;
    background-position: center center;
    @media #{$medium} {
      width: 280px;
    }
    @media #{$small} {
      width: 182px;
    }
    .date,
    .version {
      background-color: #fff;
      font-weight: bold;
      font-size: 20px;
      @media #{$medium} {
        font-size: 15px;
      }
      @media #{$small} {
        font-size: 14px;
      }
      &:nth-child(1) {
        padding-right: 10px;
      }
      &:nth-last-child(1) {
        padding-left: 10px;
      }
    }
  }
}

.main-banner,
.rg-news {
  padding: 0px 50px;
  margin-bottom: 60px;
  @media #{$medium} {
    padding: 0px 30px;
    margin-bottom: 40px;
  }
  @media #{$small} {
    padding: 0px 20px;
    margin-bottom: 40px;
  }
}

.main-banner {
  img {
    width: 100%;
  }
  .banner-pc {
    @media screen and #{$md} {
      display: none;
    }
  }
  .banner-phone {
    @media #{$mdover} {
      display: none;
    }
  }
}

.rg-news {
  .head2 {
    margin-bottom: 20px;
    &.line {
      display: flex;
      align-items: center;
      hr {
        width: 100%;
        height: 0.8px;
        background-color: #000000;
        border: 0;
        border-radius: 100px;
      }
    }
    img {
      height: 32px;
      background-color: #fff;
      padding-right: 20px;
      @media #{$medium} {
        height: 25px;
      }
      @media #{$small} {
        height: 20px;
      }
    }
  }
  .list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 40px;
    @media screen and #{$md} {
      grid-template-columns: 1fr 1fr;
      gap: 30px;
      @media #{$sm} {
        gap: 20px;
      }
    }
    .thumbnail-item {
      img {
        width: 100%;
      }
    }
    .thumbnail-title {
      width: 100%;
      margin-top: 10px;
      .text {
        font-size: 18px;
        font-weight: bold;
        @media #{$medium} {
          font-size: 16px;
        }
        @media #{$small} {
          font-size: 14px;
        }
      }
    }
    .thumbnail-sub-title {
      font-size: 14px;
      color: #666666;
      margin-top: 5px;
      @media #{$sm} {
        font-size: 13px;
      }
    }
  }
}

.new-books {
  background-color: #f2f0e8;
  padding: 60px 0px 30px;
  @media #{$small} {
    padding: 40px 0px 20px;
  }
  .books {
    margin: 0px 60px;
    @media #{$medium} {
      margin: 0px 30px;
    }
    @media #{$small} {
      margin: 0px 20px;
    }
    .head2 {
      margin-bottom: 20px;
      img {
        height: 32px;
        @media #{$medium} {
          height: 25px;
        }
        @media #{$small} {
          height: 20px;
        }
      }
    }
    .head3 {
      margin-bottom: 20px;
      &.line {
        // background-image: url("./assets/images/line.png");
        // background-repeat: repeat-x;
        // background-position: center;
        display: flex;
        align-items: center;
        hr {
          width: 100%;
          height: 0.8px;
          background-color: #000000;
          border: 0;
          border-radius: 100px;
        }
      }
      img {
        height: 25px;
        background-color: #f2f0e8;
        padding-right: 10px;
        @media #{$medium} {
          height: 18px;
        }
        @media #{$small} {
          height: 16px;
        }
      }
    }
    .eb-list,
    .pb-list {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      gap: 40px;
      height: 100%;
      margin-bottom: 30px;
      @media #{$medium} {
        grid-template-columns: 1fr 1fr 1fr;
        gap: 30px;
      }
      @media #{$small} {
        grid-template-columns: 1fr 1fr;
        gap: 20px;
      }
      .eb-item,
      .pb-item {
        .cover {
          min-height: 270px;
          display: flex;
          align-items: end;
          position: relative;
          @media #{$medium} {
            min-height: 230px;
          }
          @media #{$small} {
            min-height: 220px;
          }
          img {
            width: 100%;
          }
          .grade {
            padding: 5.5px 10px;
            position: absolute;
            bottom: 0;
            left: 0;
            color: #fff;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            &.green {
              background-color: #44cb50;
            }
            &.sea {
              background-color: #44b3cb;
            }
            &.puple {
              background-color: #9758d8;
            }
          }
        }
        .title {
          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          letter-spacing: -0.05em;
          color: #000000;
          margin: 20px 0px 10px 0px;
          @media #{$medium} {
            font-size: 16px;
            margin: 10px 0px;
          }
          @media #{$small} {
            font-size: 16px;
            margin: 10px 0px;
          }
        }
        .book-info {
          table {
            margin: 0 !important;
            padding: 0 !important;
            tr {
              td:nth-child(1) {
                width: 30px;
                font-size: 14px;
                padding-right: 10px;
                padding-left: 0 !important;
                color: #a4a4a4;
                @media #{$medium} {
                  font-size: 13px;
                  padding-right: 5px;
                }
                @media #{$small} {
                  font-size: 13px;
                  padding-right: 5px;
                }
              }
              td:nth-child(2) {
                font-size: 15px;
                @media #{$medium} {
                  font-size: 13px;
                }
                @media #{$small} {
                  font-size: 13px;
                }
              }
            }
          }
        }
      }
    }
  }
}

.rg-review {
  padding: 60px 50px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  @media #{$medium} {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 30px;
    margin-bottom: 20px;
  }
  @media #{$small} {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 20px;
    margin-bottom: 20px;
  }
  .user-tip,
  .hall_of_fame {
    min-height: 400px;
    border: 2.5px solid #cfcfce;
    padding: 6px;
    .card {
      border: 1px solid #cfcfce;
      height: 100%;
    }
    .link {
      display: flex;
      justify-content: end;
      align-items: center;
      a {
        text-decoration: none;
        color: #3e3a3a;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: -0.05em;
        margin-right: 7px;
      }
      .arrow-right {
        display: flex;
        align-items: center;
        width: 4px;
        height: 8px;
        img {
          width: 100%;
        }
      }
    }
  }
  .user-tip {
    position: relative;
    .card {
      .card-body {
        padding: 30px 54px 30px 40px;
        @media #{$medium} {
          padding: 30px;
        }
        @media #{$small} {
          padding: 30px 20px;
        }
        .head2 {
          text-align: center;
          margin-bottom: 20px;
          img {
            height: 39px;
            @media #{$medium} {
              height: 35px;
            }
            @media #{$small} {
              height: 30px;
            }
          }
        }
        .text {
          display: flex;
          gap: 30px;
          @media #{$small} {
            flex-direction: column;
            gap: 20px;
          }
          .thumb-image {
            width: 136px;
            @media screen and #{$md} {
              width: 100%;
            }
            .thumb-image-lg {
              @media screen and #{$md} {
                display: none;
              }
            }
            .thumb-image-sm {
              @media #{$mdover} {
                display: none;
              }
            }
            img {
              width: 100%;
            }
          }
          .description {
            width: 273px;
            @media #{$medium} {
              width: 100%;
            }
            @media #{$small} {
              width: 100%;
            }
            .highlight {
              display: inline-block;
              margin-bottom: 20px;
              span {
                font-size: 24px;
                font-weight: bold;
                letter-spacing: -0.05em;
                background-image: url("./assets/images/highlight_bg.png");
                background-position: 0 16px;
                background-repeat: repeat-x;
                @media #{$medium} {
                  font-size: 22px;
                }
                @media #{$small} {
                  font-size: 20px;
                }
              }
            }
            .quotation {
              width: 273px;
              margin-bottom: 10px;
              position: relative;
              @media #{$small} {
                width: 100%;
              }
              .quotation-text {
                position: relative;
                z-index: 2;
                img {
                  width: 100%;
                }
              }
              .quotes-start,
              .quotes-end {
                position: absolute;
                z-index: 1;
              }
              .quotes-start {
                top: -10px;
                left: -10px;
                img {
                  width: 67px;
                }
              }
              .quotes-end {
                bottom: -10px;
                right: 10px;
                img {
                  width: 67px;
                }
              }
            }
            p {
              font-size: 14px;
              line-height: 22px;
              letter-spacing: -0.05em;
              color: #7b7676;
              margin-bottom: 10px;
            }
          }
        }
      }
    }
    .corner {
      width: 49px;
      position: absolute;
      top: -4px;
      right: -4px;
      img {
        width: 100%;
      }
    }
  }
  .hall_of_fame {
    background-image: url("./assets/images/hall_of_fame_bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    @media #{$medium} {
      background-position: center 15px;
    }
    @media #{$small} {
      background-position: center 15px;
    }
    .card {
      .card-body {
        padding: 40px;
        @media #{$medium} {
          padding: 30px;
        }
        @media #{$small} {
          padding: 30px 20px;
        }
        .head2 {
          text-align: center;
          margin-bottom: 30px;
          img {
            height: 58px;
            @media #{$small} {
              height: 50px;
            }
          }
        }
        .line {
          width: 40px;
          margin-bottom: 30px;
        }
        .title {
          font-weight: 700;
          font-size: 24px;
          line-height: 29px;
          letter-spacing: -0.05em;
          color: #3e3a3a;
          text-align: center;
          margin-bottom: 10px;
          @media #{$small} {
            font-size: 20px;
          }
        }
        .text {
          font-weight: 500;
          font-size: 16px;
          line-height: 24px;
          letter-spacing: -0.05em;
          color: #535353;
          opacity: 0.8;
          margin-bottom: 10px;
        }
      }
    }
  }
}

.rg-story {
  padding: 0px 50px;
  margin-bottom: 60px;
  @media #{$medium} {
    padding: 0px 30px;
    margin-bottom: 40px;
  }
  @media #{$small} {
    padding: 0px 20px;
    margin-bottom: 40px;
  }
  .head2 {
    height: 32px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    @media #{$medium} {
      height: 25px;
    }
    @media #{$small} {
      height: 20px;
    }
    hr {
      width: 100%;
      height: 0.8px;
      background-color: #000000;
      border: 0;
      border-radius: 100px;
    }
    img {
      height: 100%;
      background-color: #fff;
      padding-right: 17px;
    }
  }
  .cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 40px;
    @media #{$medium} {
      grid-template-columns: 1fr 1fr;
      gap: 20px;
    }
    @media #{$small} {
      grid-template-columns: 1fr;
      gap: 20px;
    }
    .card {
      &.bg-green {
        background-color: #2fb44a;
      }
      &.bg-blue {
        background-color: #3748ee;
      }
      &.bg-red {
        background-color: #ed3023;
      }
      .tumbnail-img {
        img {
          width: 100%;
        }
      }
      .text {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 14px;
        .sentence {
          font-weight: 600;
          font-size: 14px;
          line-height: 18px;
          letter-spacing: -0.05em;
          color: #ffffff;
          margin-right: 10px;
        }
        .sns-icon {
          width: 36px;
          img {
            width: 100%;
          }
        }
      }
    }
  }
}

.bottom {
  background-color: #f5f5f5;
  padding: 30px 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media #{$medium} {
    flex-direction: column;
    padding: 30px;
    align-items: start;
  }
  @media #{$small} {
    flex-direction: column;
    padding: 30px 20px;
    align-items: start;
  }
  .company-info {
    .consent-to-receive,
    .company-address {
      &:nth-child(1) {
        margin-bottom: 10px;
      }
      max-width: 530px;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: -0.02em;
      color: #9d9d9d;
      @media #{$medium} {
        margin-bottom: 20px;
      }
      @media #{$small} {
        margin-bottom: 20px;
      }
    }
  }
  .company-logo {
    width: 227px;
    @media #{$medium} {
      width: 150px;
    }
    @media #{$small} {
      width: 130px;
    }
    img {
      width: 100%;
    }
  }
}
